  ionViewDidEnter() {



    this.addToUserData();




    //语音输入操作
    let timeOutEvent: any;
    timeOutEvent = 0;
    let _this = this;
    let orthEvent = 0;
    $("#record_input").parent().on({

      touchstart: function (e) {
        var touch = e.touches[0];

        _this.zb_y = Number(touch.pageY);
        console.log("touch.pageY=" + touch.pageY);
        orthEvent = 0;
        timeOutEvent = setTimeout(function () {
          if (orthEvent != 1) {
            _this.recordReturn = false;
            _this.record_input();//开始录音
            // alert('长按')
            return;
          }

        }, 500);

      },
      touchmove: function (e) {
        //滑动
        var touch = e.touches[0];

        _this.zb_y = Number(touch.pageY);
        console.log("touch.pageY=" + touch.pageY);

        let myy = document.documentElement.clientHeight - document.getElementById('myRecordfooter').clientHeight - document.documentElement.clientHeight * 0.03;
        //  console.log(_this.zb_y + ">" +myy)
        if (Number(_this.zb_y) > myy) {

          _this.audioSendText = "发送";
          _this.recordReturn = false;
        }
        else {
          _this.audioSendText = "取消发送";
          _this.recordReturn = true;

        }


        // alert('取消发送')
        return;

      },
      touchend: function (e) {
        var touch = e.touches[0];
        // console.log(touch.target);

        // console.log('轻击事件!')
        _this.audioSendText = "轻击事件" + _this.zb_y;
        // alert("轻击事件" + _this.zb_y);
        let myy = document.documentElement.clientHeight - document.getElementById('myRecordfooter').clientHeight - document.documentElement.clientHeight * 0.03;
        console.log(_this.zb_y + ">" + myy)
        if (_this.zb_y > myy) {

          clearTimeout(timeOutEvent);
          if (timeOutEvent != 0) {
            timeOutEvent = 0;
            orthEvent = 1;
            _this.audioSendText = " > " + _this.clientHeight;
            // alert('轻击');
            _this.recordReturn = true;
            _this.stopRecord_input(true);//发送录音
            _this.zb_y = 0;

            return;
            //alert('发送')
          } else {
            _this.recordReturn = true;
            _this.audioSendText = " < " + _this.clientHeight;
            //_this.stopRecord_input(true);//发送录音
            return;
          }
        }
        else {
          //   console.log('_this.zb_y < ' + _this.clientHeight)

          timeOutEvent = 0;
          orthEvent = 1;
          _this.stopRecord_input(false);//停止录音不发送
          clearTimeout(timeOutEvent);
        }

      }
    });


  }
