  addLocalData() {
    //加载本地数据



    if (myLocalStorage.getItem("consult_time" + this.appMin.getUserid() + '-' + this.toUserId) == null) {
      this.addMyLetterData(null, null);
      return;
    }
    else {
      let obj = myLocalStorage.getItem("consult_time" + this.appMin.getUserid() + '-' + this.toUserId)
      // console.log("consult_time="+JSON.stringify(obj));
      this.objList = obj.objList;
      this.topStartPage = obj.topStartPage;
      this.bomStartPage = obj.bomStartPage;
      this.toUserImg = myLocalStorage.getItem('toUserImg');
      setTimeout(() => {

        this.mycontent.scrollToBottom(0)
      }, 500);
      this.addMyLetterData(null, 'next')
    }




  }
  audioPaly_click(consultationId, timeLength, audioUrl) {




    // console.log('audioUrl=' + audioUrl);
    let this1 = this;
    let audioobj: any;
    let audioPayImg: any = document.getElementById('audioPayImg_' + consultationId);
    audioPayImg.src = "../../assets/img/audioPay.gif";
    //倒计时
    let timeLength_: number = timeLength;
    let timeLength1 = document.getElementById("timeLength_" + consultationId);



    //倒计时结束

    audioobj = document.getElementById("audioPaly_" + consultationId)

    if (audioobj.paused) {
      console.log('没有播放')
      audioobj.play();//开始播放
      this1.outTime = setInterval(function () {
        console.log('audioobj.paused=' + audioobj.paused)
        if (!audioobj.paused) {
          //  audioobj.pause();//暂停播放
          //  audioPayImg.src = "../../assets/img/audioPayStop.png";
          //   timeLength1.innerHTML = timeLength.toString() + "'";

          //  clearInterval(outTime);
          //audioobj.currentTime=0;
        }
        timeLength_ = timeLength_ - 1;
        timeLength1.innerHTML = timeLength_.toString() + "'";
        if (timeLength_ <= 0) {
          audioPayImg.src = "../../assets/img/audioPayStop.png";
          timeLength1.innerHTML = timeLength.toString() + "'";
          clearInterval(this1.outTime);

        }
      }, 1000);

    }
    else {
      console.log('正在播放 ')
      clearInterval(this1.outTime);
      audioobj.pause();//暂停播放
      audioPayImg.src = "../../assets/img/audioPayStop.png";
      timeLength1.innerHTML = timeLength.toString() + "'";
      audioobj.currentTime = 0;
    }

  }

